<template>
  <div class="black-bg" v-if="openModal == true">
    <transition name="listUp">
      <div class="white-bg">
          <img :src="data[index].image" style="width:100%">
          <h4>{{ data[index].title }}</h4>
          <p>{{ data[index].content }}</p>
          <p>{{ data[index].price }} 원</p>
          <button @click="$emit('closeModal')">닫기</button>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
    name : 'vue-modal',
    props : {
        data : Object,
        index : Number,
        openModal : Boolean
    },
    data (){
      return {
        month2  : 0
      }
    },
    watch : {
      inputNum(a) {
        if(a>10) {
          alert('낮은수입력')
        }

      }
    }

}
</script>

